/* @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap'); */

@font-face {
    font-family: "Montserrat";
    font-weight: 300;
    font-style: normal;
    src: url('./fonts/Montserrat-Light.woff2') format('woff2'),
         url('./fonts/Montserrat-Light.woff') format('woff');
}

@font-face {
    font-family: "Montserrat";
    font-weight: 400;
    font-style: normal;
    src: url('./fonts/Montserrat-Regular.woff2') format('woff2'),
         url('./fonts/Montserrat-Regular.woff') format('woff');
}

header {
    /* position: fixed;
    width: 100vw; */
    z-index: 10000;
    background: rgba(0, 128, 128, .8);
}
.header__wrapper {
    display: flex;
    justify-content: space-between;
    transition: all 0.4s ease;
    max-width: 1140px;
    margin: 0 auto;
    position: relative;
}
.header__wrapper h1 a {
    text-decoration: none;
    color: white;
}

header h1,
.registration_modal h1 {
    font-size: 26px;
    color: white;
    margin: 0;
    padding-left: 20px;
    line-height: 40px;
}

header ul {
    margin: 0;
}

header ul {
    list-style: none;
    display: flex;
    height: 40px;
    justify-content: right;
    padding-right: 10px;
}
header ul li {
    line-height: 40px;
}

header ul li:nth-of-type(2) {
    margin-right: 20px;
}

header ul li:nth-of-type(2)::after {
    content: '';
    position: absolute;
    border-right: 3px solid white;
    height: 40px;
    top: 0;
    margin-left: 98px;
    transform: skewX(-10deg);
}

header ul li:hover {
    background: rgba(0, 128, 128, 1);
    cursor: pointer;
}

header ul a {
    display: block; 
    padding: 0 10px;
    color: white;
    text-decoration: none;
    height: 100%;
}

.body_hover {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    /* transform: translate(-21%, -18%); */
    z-index: 1000;
    background: rgba(0,0,0,0.5);
}

.user_modal {
    /* display: none; */
    position: absolute;
    background: white;
    width: 700px;
    height: 800px;
    border: 1px solid rgba(0, 128, 128, 0.8);
    z-index: 1010;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    background-image: url('../media/modal_bg.jpg');
    background-size: contain;
}

.close_modal {
    font-size: 30px;
    line-height: 30px; 
    position: absolute;
    top:0;
    right: 10px;
    cursor: pointer;
}

.user_modal h1 {
    color:rgba(0, 128, 128, 1);
    max-width: 500px;
    margin-left: 76px;
    margin-bottom: 40px;
}

.user_modal form {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: -20px;    
}

.user_modal form h6 {
    text-align: center;
    font-size: 24px;
    margin: 0 auto 40px auto;
    color: rgba(0, 128, 128, 0.8);
}

.user_modal input {
    width: 500px;
    height: 30px;
    border-radius: 4px;
    border: 1px solid rgba(0, 128, 128, 0.8);
    margin: 10px auto;
    box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.2);
    padding: 10px;
    font-size: 16px;
    color:rgba(0, 128, 128, .8);
}
.user_modal input::placeholder {
    font-size: 16px;
}
.user_modal button {
    width: 524px;
    margin: 20px auto 0 auto !important;
}

.main {
    display: flex;
    justify-content: center;
    margin: 0 auto 20px auto;
    position: relative;
    max-width: 1200px;
    background: #fff;
}

.converter:nth-of-type(2) {
    margin-top: 20px;
}
p, h3, h5, h4 {
    margin: 0;
    padding: 0;
}
body {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    overflow: scroll;
    overflow-x: hidden;
    /* background-image: url('../media/modal_bg.jpg'); */
}
section {
    display: flex;
    margin: 0 auto;
    flex-direction: column;
}
.list_wrapper {
    display: flex;
    flex-direction: column;
}
.coin_list {
    text-align: center;
    transition: all 0.3s linear;
}
.coin_body {
    display: flex;
    text-align: left;
    justify-content: space-between;
    padding-right: 10px;
}
.coin_price {
    display: flex;
}

.coin_body h4 {
    margin-bottom: 5px;
    margin-right: 5px;
}
.coin_cap {
    display: flex;
}
.coin {
    display: flex;
    max-width:100%;
    margin: 5px 0;
    border: 1px solid teal;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    border-radius:5px 4px;
    cursor: pointer;
    transition: all 0.2s linear;
}
.coin:hover {
    transform: translate(4px, -4px);
}
.search_coin:hover {
    transform: translate(0, 0);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.coin_order {
    display: flex;
    justify-content: center;
    align-items: center;
}

.coin_header img {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    width: 25px;
    height: 25px;
    margin-left: 10px;
}

.coin_header {
    display: flex;
}
.coin_header h3 {
    line-height: 25px;
}
.coin_order {
    flex: 1;
}
.coin_wrapper {
    flex: 7;
    text-align: center;
}

.converter {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: all 0.3s linear;
}
.converter h3 {
    margin-bottom: 10px;
}
.converter label,
.calculator_header {
    display: block;
    font-size: 1.5em;
    font-weight: bold;
    color: teal;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.01);
}

input, label, select {
    margin-bottom: 10px;
}
input {
    font-family: 'Montserrat', sans-serif;
}

.average_container {
    display: flex;
    align-items: flex-end;
}
.average_container__inner {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-right: 5px;
}
.average_container__inner:last-of-type {
    margin-right: 0;
}

.btn {
    border: none;
    border-radius: 4px;
    cursor: pointer;
    background-color: rgba(0, 128, 128, 0.8);
    color: white;
    padding: 10px 20px;
    margin-right: 10px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
    font-family: 'Montserrat', sans-serif;
    transition: all 0.3s ease;
}
.btn:last-of-type {
    margin-right: 0;
}
.btn:hover {
    background-color: rgba(0, 128, 128, 1);
}
.converter input::-webkit-outer-spin-button,
.converter input::-webkit-inner-spin-button,
.portfolio_input::-webkit-outer-spin-button, 
.portfolio_input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.converter input {
    height: 24px;
    border-radius: 4px;
    border: 1px solid grey;
    padding: 0 6px;
}
.converter input:focus {
    border: 1px solid rgba(0, 128, 128, 0.1);
}
select {
    font-family: 'Montserrat', sans-serif;
    padding: 2px 6px;
    border-radius: 4px;
    height: 24px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    position: relative;
    z-index: 1;
    font-size: 16px;
    background: url('../media/arr.png') no-repeat right 5px center / 11px auto;
    border: 1px solid teal;
}

::-webkit-scrollbar {
    background: transparent;
    border-radius: 2px;  
}

::-webkit-scrollbar-thumb {
    background: rgba(0, 128, 128, 0.2); 
    border-radius: 2px;  
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 102, 128, 0.247); 
}

.average_container input {
    max-width: 188px;
    min-width: 169px;
}

input::placeholder {
    font-family: 'Montserrat', sans-serif;
}

.crypto_input::placeholder {
    padding-left: 2px;
    color:rgba(0, 0, 0, 0.5);
    font-size: 16px;
}

h3 {
    color: rgba(0, 102, 128, 1);
    text-shadow: 1px 1px 0.1px rgba(0, 0, 0, 0.01);
}

.btn_container {
    margin-top: 10px;
    display: flex;
}

input:focus,
select:focus {
    outline: 1px solid teal;
}

.reverse_wrapper {
    display: flex;
    align-items: flex-start;
    margin-top: 10px;
    margin-bottom: 10px;
    justify-content: space-between;
}

.reverse_btn {
    font-family: 'Montserrat', sans-serif;
    /* position: absolute; */
    /* top: 196px; */
    /* right: 0; */
    border: 1px solid teal;
    border-radius: 4px;
    background: transparent;
    height: 24px;
    padding: 0 22px;
    cursor: pointer;
    transition: all 0.2s ease;
    color: teal;
    transition: all 0.5s ease;
}

.reverse_btn:hover {
    background: rgba(0, 102, 128, 0.5);
    box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.3);
    border: 1px solid transparent;
}
.filters {
    display: flex;
    justify-content: center;
}
.filters button {
    flex: 1;
    font-family: 'Montserrat', sans-serif;
}
.filters button:nth-of-type(2),
.filters button:nth-of-type(3) {
    padding-top: 8px;
}
.filters button:nth-of-type(3) {
    margin-right: 0;
}
.search_form {
    display: flex;
    margin-top: 20px;
}
.search_form .btn {
    margin-left: 10px;
}
.search {
    height: 30px;
    border-radius: 4px;
    border: 1px solid teal;
    padding-left: 5px;
    flex: 4;
}
.search::placeholder {
    font-family: 'Montserrat', sans-serif;
    display: block;
    position: relative;
    top: 2px;
    font-size: 16px;
}

.search_form button {
    height: 34px;
    margin-right: 0;
}
.features_container {
    padding-top: 16px;
    display: flex;
    margin: 0 auto;
    flex-direction: column;
    align-items: center;
}
h2 {
    color: teal;
    margin: 10px auto;
}
.price_pos {
    flex: 3;
    text-align: right;
    line-height: 24px;
    padding-right: 8px;
    padding-top: 2px;
    color: teal;
}
.price_neg {
    flex: 3;
    text-align: right;
    line-height: 24px;
    padding-right: 8px;
    padding-top: 2px;
    color: red;
}
.header {
    font-size: 30px;
    display: flex;
    margin: 10px 0;
}
.sort_header {
    font-size: 20px;
}
.crypto_info {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    margin: 50px auto 0 auto;

}
.crypto_info li {
    list-style: none;
}
.crypto_info a {
    color: teal;
}
.crypto_data {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.crypto_data div {
    flex: 1;
}
.crypto_data ul {
    padding-left: 0;
}

.crypto_header {
    display: flex;
    max-width: 400px;
    margin: 0 auto;
    justify-content: center;
}

.crypto__descr {
    flex-direction: column;
    text-align: justify;
    margin-bottom: 20px;
}
.crypto__descr p {
    margin-top: 20px;
    text-indent: 25px;
}

.crypto_header h2 {
    margin: 0;
    line-height: 50px;
    margin-right: 20px;
}
.crypto_header img {
    width: 50px;
    height: 50px;
}
.table {
    display: flex;
    flex-direction: column;
    border: 1px solid teal;
    flex-shrink: 3;
    border-radius: 4px;
    margin-bottom: 20px;
}

.thead {
    display: flex;
    border-bottom: 1px solid teal;
    background: rgba(0, 128, 128, 0.5);
    text-align: center;
}

.thead .td{
    cursor: pointer;
}
.thead .td:hover {
    background: rgba(0, 128, 128, 0.7);
}

.tbody {
    display: flex;
    flex-direction: column;
}

.tbody div {
    display: flex;
}

.td {
    padding-top: 10px;
    min-height: 30px;
    border-right: 1px solid teal;
    flex: 1;
    justify-content: center;
    text-align: center;
}

.td:last-of-type {
    border-right: none;
}

.tr {
    border-bottom: 1px solid teal;
}

.tbody .tr:last-of-type{
    border-bottom: none;
}
.market_data {
    transition: all 0.2s ease;
}
.market_data h4 {
    display: flex;
    justify-content: space-between;
}
.market_data h4 span:last-child {
    color: teal;
}

.crypto_info h3 {
    font-size: 22px;
}
.crypto_info h3:last-of-type {
    margin-bottom: 10px;
}
.ath {
    position: relative;
}
.ath::after {
    content: '';
    position: absolute;
    top: 17px;
    left: 0;
    width: 33px;
    height: 2px;
    border-top: 2px dashed rgba(0, 128, 128, 0.6);
}

.ath_hint {
    position: absolute;
    top: -40px;
    left: 0;
    width: 140px;
    height: 30px;
    background: rgba(0, 128, 128, .8);
    display: none;
    z-index: 100;
    border-radius: 20px 4px 20px 4px;
    line-height: 30px;
    text-align: center;
    color: white;
    font-size: 14px;
    font-weight: 300;
}
.ath_hint:after {
    box-sizing: border-box;
    content: '';
    position: absolute;
    bottom: -20px;
    left: 6px;
    width: 20px;
    height: 20px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-top: 10px solid rgba(0, 128, 128, .8);
}
.ath:hover > .ath_hint {
    display: block;
}
.coin {
    text-decoration: none;
    color: black;
}
.paginator_wrapper {
    text-align: center;
    margin-top: 5px;
}

.paginator_wrapper .btn {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-top: 0;
}

.features {
    position: absolute;
    top: 17px;
    min-width: 200px;
    height: 30px;
    padding-right: 20px;
    background-color: rgba(0, 128, 128, 0.8);
    color:white;
    font-family: 'Montserrat', sans-serif;
    align-self: flex-end;
}
.features option {
    height: 30px;
}
select.features option:hover {
    box-shadow: 0 0 10px 100px #198d8d inset; 
}
.current_price {
    max-width: 280px;
}
.btn_back,
.btn_top {
    display: none;
}

footer {
    display: flex;
    width: calc(100% - 20px);
    box-sizing: border-box;
    flex-direction: column;
    max-width: 1105px;
    margin: 0 auto;
    text-align: justify;
    padding: 20px 40px;
    background: rgba(128,128,128, 0.2);
    border-radius: 4px;
}
footer span {
    color: red;
}

.spinner {
    display: block;
    margin: 0 auto;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.chart_container iframe {
    min-width: 100%;
    display: flex;
    max-width: 100vw;
}

.select__wrapper {
    transition: all 0.5s ease;
}

select.fiat_select {
    width: 100%;
    border: 1px solid teal;
    background: none;
}
.select_arrow {
    position: absolute;
    right: 0;
    background: url('../media/arr1.png') no-repeat right 7px center / 11px auto;
    height: 24px;
    width: 24px;
    transition: all 0.2s ease;
}
.hide {
    display: none;
}
.select_container {
    width: 100%;
    margin: 0 auto;
    display: flex;
    margin-right: 10px;
    justify-content: right;
}
.select_container select {
    margin-right: 20px;
}
.features {
    display: none;
}

.portfolio {
    margin: 0 auto;
    max-width: 900px;
    /* overflow-x: hidden; */
}

.portfolio h1 {
    color: teal;
    text-transform: uppercase;
    letter-spacing: 30px;
}

.portfolio__container {
    max-width: 900px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}

.portfolio__graph_container {
    /* max-width: 90%; */
    min-width: 90%;
    margin: 0 auto;
    height: 300px;
    border: 1px solid teal;
    overflow: hidden;
    position: relative;
    background-image: url('../media/graphic.png');
    background-size:cover;
    background-position: 50% 50%;
    background-repeat:no-repeat;
}

.portfolio__graph_container img {
    height: 100%;
    width: 100%;
}
.portfolio__table_link {
    justify-content: space-between;
    padding: 10px 0 !important;
    align-items: center;
}
.portfolio__table_link span {
    padding: 10px;
    flex: 1;
}

.portfolio__form {
    text-align: center;
    position: absolute;
    z-index: 1010;
    background: white;
    border: 1px solid teal;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 500px;
    width: 380px;
    border-radius: 8px;
    flex-direction: column;
    justify-content: center;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
}

.portfolio__form_container label {
    box-shadow: none !important;
}

.portfolio__form input,  
.portfolio__form p,  
.portfolio__form label  {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.portfolio__form h4 {
    font-size: 18px;
    margin-bottom: 20px;
}

.portfolio__form form {
    margin-bottom: 20px;
}

.portfolio__form_container {
    display: flex;
    margin: 0 auto;
    max-width: 91%;
}

.portfolio__form_container label {
    font-weight: bold;
}

.portfolio__form_container div {
    display: flex;
    flex-direction: column;
}

.portfolio__form form label {
    display: inline-block;
    padding: 6px 40px;
    border: 1px solid teal;
    border-radius: 4px;
    transition: all 0.3s ease;
}

.portfolio__form form label:nth-of-type(1) {
    margin-right: 10px;
}

.portfolio__form form label:hover {
    background: rgba(0, 128, 128, 0.6);
    color: white;
}

.portfolio__form input:checked + label {
    background: rgba(0, 128,128, 0.8);
    color: white;
}

.portfolio__form_type {
    display: none;
}

.portfolio__table {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.portfolio__table_header,
.portfolio__table_item {
    display: flex;
    justify-content: space-between;
    /* padding: 10px; */
}

.portfolio__table_header span,
.portfolio__table_item a,
.portfolio__table_item span {
    /* flex: 1; */
    cursor: pointer;
    padding: 10px;
    overflow: hidden;
}

.portfolio__table_item a:nth-of-type(1),
.portfolio__table_item span {
    flex: 0.87;
}
.portfolio__table_item a:nth-of-type(2) {
    flex: 4
}
.portfolio__table_header span {
    flex: 1
}

.portfolio__table_item span span {
    padding: 4px;
}


.portfolio__table_header {
    border-top: 1px solid rgba(128, 128, 128, 0.3);
    border-bottom: 1px solid rgba(128, 128, 128, 0.3);
    color:rgba(0, 128, 128, 1);
    display: flex;
    max-width: 100%;
    align-items: center
}

.portfolio__table_item {
    align-items: center;
    border-bottom: 1px solid rgba(128, 128, 128, 0.3);
    overflow: hidden;
}

.portfolio__table_item span:nth-of-type(2),
.portfolio__table_item span:nth-of-type(4) {
    margin-top: -5px;
}

.portfolio_input {
    border: 1px solid teal;
    height: 25px;
    border-radius: 4px;
    padding: 4px 6px;
    width: 80%;
    margin: 0 auto;
    font-family: 'Montserrat', sans-serif;
}
.portfolio__form_result span {
    display: block;
    margin: 20px auto;
    font-weight: bold;
}
.portfolio__form_result p {
    border: 1px solid teal;
    border-radius: 4px;
    width: 85%;
    margin: 0 auto 20px auto;
    height: 40px;
    color: teal;
    padding: 20px 0 0 0;
}

.portfolio__form .btn {
    width: 85%;
    margin: 0 auto;
}

.portfolio__search {
    min-width: 350px;
    /* height: 500px; */
    border: 1px solid rgba(0, 102, 128, 1);
    border-radius: 8px;
    /* display: flex; */
    flex-direction: column;
    justify-content: center;
    max-height: 500px;
    padding: 20px 0;
    margin: 0 auto;
    position: absolute;
    z-index: 1011;
    background: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.portfolio__search_wrapper {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 1px;
    overflow-y: scroll;
}

.portfolio__search form {
    width: 80%;
    margin: 0 auto 10px auto; 
}

.portfolio__search ul {
    list-style: none;
    width: 80%;
}

.portfolio__search li {
    text-align: left;
    padding: 5px 10px;
    border-top: 1px solid rgba(128, 128, 128, 0.5);
    cursor: pointer;
    transition: all 0.1s ease;
}
.portfolio__search li:last-of-type {
    border-bottom: 1px solid rgba(128, 128, 128, 0.5);
}

.portfolio__search li:hover {
    background: rgba(0, 128,128, 0.8);
    color: white;
}
.portfolio__currency_add {
    margin-bottom: 20px;
}
.portfolio__currency_add span {
    display: inline-block;
    margin-right: 10px;
    line-height: 36px;
}
.portfolio__currency_add button {
    font-size: 20px;
}

.portfolio__actions {
