@media screen and (max-width:1042px) {
    .main {
        flex-direction: column;
    }
    section {
        width: 100%;
    }
    .features {
        display: flex;
    }
    .list_wrapper {
        padding: 0 20px;
    }
    .features_container {
        display: none;
        margin: 0;
        padding: 16px 20px 0 20px;
    }
    .average_container__inner {
        width: calc(50% - 20px);
    }
    .average_container__inner:nth-of-type(1) {
        padding-right: 5px;
    }
    .average_container__inner:nth-of-type(2) {
        padding-left: 5px;
    }
    .features_container input,
    .features_container select {
        height: 25px;
    }
    .features_container .features {
        height: 30px;
    }
    .coin_body {
        flex-direction: column;
    }
    .average_container input {
        max-width: 100%;
    }
    .reverse_btn {
        top: 204px;
    }
    .crypto_info {
        padding-left: 20px;
        padding-right: 20px;
    }
    .crypto_info>a {
        display: none;
    }
    .btn_container {
        justify-content: right;
    }
    .btn_back,
    .btn_top {
        position: sticky;
        height: 30px;
        margin-top: -40px;
        width: 100%;
        line-height: 30px;
        padding: 0 20px;
        font-family: 'Montserrat', sans-serif;
    }
    .btn_back {
        display: none;
        margin-bottom: 20px;
    }
    .btn_top {
        display: block;
    }
    .btn_back:hover,
    .btn_top:hover {
        background: rgba(0, 128, 128, 1);
    }
    .btn_top {
        bottom: 10px;
    }
    .btn_back {
        top: 10px;
        display: block;
    }
    .btn_back a {
        color: white;
        text-decoration: none;
    }
    .btn_back::before {
        content: '';
        background-image: url('../media/arr.png');
        position: absolute;
        background-repeat: no-repeat;
        background-size: contain;
        height: 10px;
        width: 10px;
        top: 10px;
        left: 32px;
        transform: rotate(90deg);
    }
    .btn_top::before {
        content: '';
        background-image: url('../media/arr.png');
        position: absolute;
        background-repeat: no-repeat;
        background-size: contain;
        height: 10px;
        width: 10px;
        top: 10px;
        right: 134px;
        transform: rotate(180deg);
    }
    footer {
        width: calc(100% - 40px);
    }

}
